
import { Component, Vue, Prop } from 'vue-property-decorator'

@Component({
  name: 'GanRange'
})
export default class GanRange extends Vue {
  @Prop({ default: 50 }) value!: number | string

  render(h: Function) {
    return h('input', {
      attrs: {
        type: 'range',
        value: this.value
      },
      class: [
        'gan-range'
      ],
      on: {
        change: (event: any) => {
          this.$emit('change', event.target.value)
          this.$emit('input', event.target.value)
        }
      }
    })
  }
}
